.banner{
    color: white;
    object-fit: contain;
    height:448px;
    /* filter: blur(8px);
    -webkit-filter: blur(0.5px); */
}

.banner_content{
    margin-left: 30px;
    padding-top: 140px;
    height:190px;

}

.banner_title{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}

.banner_description{
    width:45rem;
    line-height:1.4;
    padding-top: 1rem;
    font-size: 0.8rem;
    max-width: 360px;
    height:80px
}

.banner_button{
    cursor:pointer;
    color:white;
    outline: none;
    border:none;
    font-weight:700;
    border-radius:0.2vw;
    padding:0.5rem 2rem;
    background-color: rgba(51,51,51,0.5);
}

.banner_button:hover{
    color:black;
    background-color: white;
    transition: all 0.2s;
}

.fade{
    height:7.4rem;
    background-image: linear-gradient(180deg,transparent,rgba(37,37,37,0.61),#111);
}